.home-class{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  text-align: center;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-line-pack: center;
      align-content: center;
  height: 100%;  
}
.home-class header {
  -ms-flex: auto 1 1;
      flex: auto 1 1;
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}
.home-class p {
  -ms-flex: auto 1 1;
      flex: auto 1 1;
  color: #000000;
  font-size: 18px;
}
.home-class div {
  -ms-flex: auto 1 1;
      flex: auto 1 1;
  /* background-color: beige; */
}

.login {
  margin: 150px auto;
  text-align: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  color: #222;
  font-size: 18px;
}
.login span {
  font-size: 16px;
}


.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

ul {
  /* background-color: aqua; */
  text-align: left;
}



